import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
//import Button from '@material-ui/core/Button';

const styles = theme => ({
  button: {
    display: 'block',
    marginTop: theme.spacing.unit * 2,
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
  },
});

class SelectPlayer extends React.Component {
  state = {
   // age: '',
    open: false,
  };

  handleChange = event => {
      //alert(this.props.SelectPlayer);
      this.props.setSelPlayer(event);
    //this.setState({ [event.target.name]: event.target.value });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleOpen = () => {
    this.setState({ open: true });
  };

  render() {
    const { classes } = this.props;

    return (
      <form autoComplete="off">
        {/*<Button className={classes.button} onClick={this.handleOpen}>
          Open the select
    </Button>*/}
        <FormControl className={classes.formControl}>
          <InputLabel htmlFor="controlled-open-select">Players</InputLabel>
          <Select
            open={this.state.open}
            onClose={this.handleClose}
            onOpen={this.handleOpen}
            value={this.props.selPlayer}
            onChange={this.handleChange}
            inputProps={{
              name: 'players',
              id: 'controlled-open-select',
            }}
          >        
            <MenuItem value="1">video-react</MenuItem>
            <MenuItem value="2">react-player</MenuItem>
          </Select>
        </FormControl>
      </form>
    );
  }
}

SelectPlayer.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SelectPlayer);


