import React from 'react';

import "./video-react.css";

import {
  Player,
  BigPlayButton,
  ControlBar,
  PlayToggle,
  FullscreenToggle,
  CurrentTimeDisplay
} from 'video-react';

const Player2=props=> {
  return(
  <Player
    autoPlay
    src={props.url}>
    <BigPlayButton position="center" />
    <ControlBar autoHide={true} disableDefaultControls={true}>
      <PlayToggle />
      <FullscreenToggle />
      <CurrentTimeDisplay />
    </ControlBar>
  </Player>)
}
export default Player2


