import React from 'react';

import Player1 from './Player1'
import Player2 from './Player2'


import InfoPanel from "./InfoPanel";

const Player = props => {
    let pl = <div></div>;
    let ip = <div />;
    if (props.channelInfo.url !== "") {
        switch (props.selPlayer) {
            case "2":
                pl = <Player2 url={props.channelInfo.url} />
                break
            default:
                pl = <Player1 url={props.channelInfo.url} />
        }
        ip = <div>
            <InfoPanel channelInfo={props.channelInfo} />
        </div>

    }
    return (
        <div>
            {pl}
            {ip}
        </div>
    )
}
export default Player



