import React from 'react';

import List from '@material-ui/core/List';
import { Link } from 'react-router-dom'
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import Avatar from '@material-ui/core/Avatar';

//import axios from 'axios'
//import config from '../../config';
//import tvs from './tvs'
//import { color } from '@material-ui/core/colors';
import deepOrange from '@material-ui/core/colors/deepOrange';
import deepPurple from '@material-ui/core/colors/deepPurple';

const styles = theme => ({
    active: {
        backgroundColor: '#bbb',
        color: '#fff'
    },
    avatar: {
        /*margin: 10, */
    },
    orangeAvatar: {
        /*margin: 10, */
        color: '#fff',
        backgroundColor: deepOrange[500],
    },
    purpleAvatar: {
        
        color: '#fff',
        backgroundColor: deepPurple[500],
    },
    row: {
        display: 'flex',
        justifyContent: 'center',
    },
})
class ListChannels extends React.Component {
    constructor(props) {
        super(props);
        this.state = { selMenu: null };

    }
    componentDidMount() {
        //this.props.setUrlPlaylist(this.props.match.url)
        
    }
    componentDidUpdate() {
     
    }
    //handelClick = event => {

        //const key = parseInt(event.currentTarget.getAttribute("data-key"),10);
        //this.setState({ selMenu: key })
        //const url = event.currentTarget.getAttribute("date-to")
        //console.log(to)
        

    //}
    handelClick = event => {

        //const key = parseInt(event.currentTarget.getAttribute("data-key"),10);
        //this.setState({ selMenu: key })
        //const url = event.currentTarget.getAttribute("data-url")
        //this.props.setUrlChannel(event)
      //  const id = event.currentTarget.getAttribute("data-id")
    
        //this.props.setUrlChannel(id)

    }
    render() {
        const { classes } = this.props;
        //let list = [];

        //const className=classes.active;
        
        const MyLink = (props) => <Link   {...props} />
        //console.log(this.props.match.url);
        const listItems = this.props.plsList.map((tv, index) =>

            <ListItem component={MyLink} data-url={tv.url} data-id={tv.id} key={index} data-key={index} button onClick={this.handelClick}  to={this.props.match.url+"/"+tv.id}>
                <Avatar  className={classes.avatar}>{tv.name.charAt(0)}</Avatar>
                {/* src=config.imgs+"/video.png" */}
                <ListItemText primary={tv.name} />
            </ListItem>
        );
        return (
            <List key={this.props.urlChannelList} component="nav">
                {listItems}
            </List>
        )
    }
}
ListChannels.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(ListChannels);


