import React from 'react'
import IconButton from '@material-ui/core/IconButton';
import Star from '@material-ui/icons/Star';
import StarBorder from '@material-ui/icons/StarBorder';
//import CircularProgress from '@material-ui/core/CircularProgress';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import axios from 'axios'
//import fetch from 'fetch'
import config from '../../config';

const styles = theme => ({
    progress: {
        margin: theme.spacing.unit * 2,
    },
});
class Favorite extends React.Component {
    //const { props } = this.props;
    constructor(props) {
        super(props);
        //this.state = { favorite: 0 }
        //console.log(this.props.channelInfo);
        this.state = {
            favorite: this.props.channelInfo.favorite.toString(),
            completed: 0,
        };

    }
    componentWillReceiveProps(nextProps) {
        this.setState({
            favorite: nextProps.channelInfo.favorite.toString()

        });
        //console.log(nextProps.channelInfo);
    }
    componentWillMount() {
        //this.setState({ favorite: this.props.channelInfo.favorite.toString() })
        //console.log(this.state.favorite);

    }
    handleClick = event => {
        let id = event.target.dataset.id;
        //console.log(id + " " + event.target.dataset.favorite);
        let th = this;
        let url = config.api + "/favorite/" + id

        if (event.target.dataset.favorite === "0") {
            //alert(url);
            //alert("ha"+event.target.dataset.id);


            //axios.defaults.withCredentials = true;
            //let content = 'Hello world'
            /* axios(url,{
                 method: 'GET',
                 mode: 'no-cors',
                 headers: {
                   'Access-Control-Allow-Origin': '*',
                   'Content-Type': 'application/json',
                 },
                 withCredentials: true,
                 credentials: 'same-origin',
               }) 
              
            // axios(axiosConfig)
                 .then(function (response) {
                     //th.setState({ channelInfo: response.data, });
                     console.log(response.data);
                 })
                 .catch(function (error) {
                     console.log(error);
                 });
                  */
            //axios.post(url,{
            //    oops: "PUT"
            //  });

            axios.put(url)
                .then(function (response) {
                    //th.setState({ channelInfo: response.data, });
                    if (response.data.id) {
                        th.setState({ favorite: "1" });
                    }
                    //console.log(response.data.id);
                })
                .catch(function (error) {
                    console.log(error);
                });

        } else {
            axios.delete(url)
                .then(function (response) {
                    //th.setState({ channelInfo: response.data, });
                    //console.log(response.data.id);
                    if (response.data.id === undefined) {
                        th.setState({ favorite: "0" });
                    }
                    //console.log(response);
                })
                .catch(function (error) {
                    console.log(error);
                });
        }
    }
    render() {
        //const { classes } = this.props;
        //const star = 
        const star = (this.state.favorite === "1") ? (<Star />) : (<StarBorder />);

        return (
            <IconButton data-id={this.props.channelInfo.id} data-favorite={this.state.favorite} onClick={this.handleClick} >
                {star}

            </IconButton >
        )

    }
}
Favorite.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Favorite);



