import React from 'react';
import { Link } from 'react-router-dom'
import Button from '@material-ui/core/Button';

import axios from 'axios'
import config from '../../config';

//import MyButton from './MyButton';
class Playlists extends React.Component {
    constructor(props) {
        super(props);
        this.state = { plsList: [] }
    }
    handelClick = event => {

        //const key = parseInt(event.currentTarget.getAttribute("data-key"),10);
        //this.setState({ selMenu: key })
        const url = event.currentTarget.getAttribute("date-to")
        //console.log(to)
        this.props.setUrlPlaylist(url)

    }
    componentDidMount() {
        let th = this
        axios.get(config.api + "/playlists")
            .then(function (response) {
                th.setState({ plsList: response.data });
            })
            .catch(function (error) {
                console.log(error);
            });
    }
    render() {
        const MyLink = (props) => <Link   {...props} />
        const listItems = this.state.plsList.map((tv, index) =>
            <Button component={MyLink} key={index} onClick={this.handelClick}  to={"/playlist/" + tv.id} date-to={"/playlist/" + tv.id} >
            {tv.name}
            </Button>
        )
        return (
            <div>
                <Button component={MyLink} onClick={this.handelClick} key="favorite" color="primary"  to={"/favorite"} date-to={"/favorite"} >
                Favorite
                </Button>
                {listItems}
            </div>
        )
    }
}
export default Playlists;


