import React, { Component } from 'react';

import { BrowserRouter as Router, Route } from "react-router-dom";

import axios from 'axios'
import config from '../../config';

import Player from './Player'
import ListChannels from './ListChannels'
import SelectPlayer from './SelectPlayer'
//import SelectPlaylist from './SelectPlaylist'
import Playlists from './Playlists';



//import "video-react/dist/video-react.css";
import "./App.css";






class VideoPls extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // url: "",
      selPlayer: "1",
      plsList: [],
      urlChannelList: "",
      channelSelect: 0,
      channelInfo: { url: "", name: "" }
    };
    this.setUrlChannel = this.setUrlChannel.bind(this);
    this.setSelPlayer = this.setSelPlayer.bind(this);
    this.setUrlPlaylist = this.setUrlPlaylist.bind(this);
    //this.handleOnReady = this.handleClick.bind(this);
    // this.videoRef.presentFullscreenPlayer()

  }
  // setUrlChannel(url) {
  setUrlChannel(id) {
    // console.log(event);
    //const url = event.currentTarget.getAttribute("data-url")
    //const id = event.currentTarget.getAttribute("data-id")
    let th = this;
    axios.get(config.api + "/channel/" + id)
      .then(function (response) {
        th.setState({ channelInfo: response.data, channelSelect: id });
        //console.log(th.state.channelInfo.name);
      })
      .catch(function (error) {
        console.log(error);
      });
    //console.log(id);
    //this.props.setUrlChannel(url)
    //this.setState({ url: url });
  }
  setUrlPlaylist(url) {
    //console.log(url);
    let th = this
    axios.get(config.api + url)
      .then(function (response) {
        th.setState({ plsList: response.data });
      })
      .catch(function (error) {
        console.log(error);
      });
    this.setState({ urlChannelList: url });
  }
  setSelPlayer = event => {
    //console.log("ha"+event.target.value);
    this.setState({ selPlayer: event.target.value });
  }
  handleOnReady() {
    console.log("ha");
  }
  handleSelectPlayer = name => event => {
    this.setState({ [name]: event.target.value });
  };
  componentDidMount() {
    // this.setUrlPlaylist(this.state.urlChannelList)
  }
  render() {

    //const selectPlayer = (
    //  
    //)
    //const favoriteDisable = (this.state.channelInfo.url === "") ? true : false;
    const MyListChannels = (props) => {
      if (this.state.urlChannelList !== props.match.url) {
        this.setState({ urlChannelList: props.match.url });
        this.setUrlPlaylist(props.match.url);
      }
      return (
        <ListChannels {...props} plsList={this.state.plsList} setUrlChannel={this.setUrlChannel} />
      )
    }
    const SetChannel = (props) => {
      //console.log(props.match.params.channel);
      if (this.state.channelSelect !== props.match.params.channel) {
        this.setUrlChannel(props.match.params.channel)
      }
      return (
        <div />
      )
    }
/*    const MyPlayer = (props) => {
      //console.log(props.match.params.channel);
      if (this.state.channelSelect ==0) {
        this.setUrlChannel(props.match.params.channel)
      }
      return (
        <Player {...props} selPlayer={this.state.selPlayer} channelInfo={this.state.channelInfo} />
      )
    }
    */
    return (
      <Router>
        <div className="App">
          <div className='player-wrapper'>

            <Route path="/favorite/:channel" component={SetChannel} />
            <Route path="/playlist/:pls/:channel" component={SetChannel} />
            <Player selPlayer={this.state.selPlayer} channelInfo={this.state.channelInfo} />

          </div>
          <div className="menu-channels">
            <div className="select-player">
              <SelectPlayer selPlayer={this.state.selPlayer} setSelPlayer={this.setSelPlayer} />
            </div>
            <div className="pls-select">
              <Playlists setUrlPlaylist={this.setUrlPlaylist} />
            </div>
            <div className="channels-list">
              <Route path="/playlist/:pls" component={MyListChannels} />
              <Route path="/favorite" component={MyListChannels} />
            </div>
          </div>

        </div>
      </Router>
    );
  }
}

export default VideoPls;



